.calculator-wrap {
    margin-top: 50px;
}

.calculator {
    border-radius: 10px;
    margin: 0 auto;
    padding: 15px;
    width: 240px;
    border: 2px solid black;
    background-color: black;
    display: flex;
    font-display: row;
    flex-wrap: wrap;
    justify-content: center;
}
.calculator-display {
    width: 190px;
    height: 40px;
    padding: 0 10px;
    border: 2px solid white;
    border-radius: 15px;
    background-color: black;
    color: white;
    font-size: 25px;
    text-align: right;
}

.calculator-buttons {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 5px;
    flex-direction: row;
    flex-wrap: wrap;

    button {
        border: none;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        font-size: 20px;
        background-color: #3b3333;
        color: white;
    }
    button:active {
        background-color: #f03e3e;
    }
    .long {
        width: 105px;
        border-radius: 30px;
    }
    .grey {
        background-color: grey;
    }
    .orange {
        background-color: orange;
    }
    .active {
        background-color: white;
        color: orange;
    }
}
